import React, { useState, useEffect } from "react";
import {
  Table,
  Button,
  Select,
  Card,
  Input,
  message,
  Row,
  Col,
  Modal,
  DatePicker
} from "antd";
import { Api, baseURL } from "../api";
import qs from "qs";
import { Link } from "react-router-dom";
import OrderInfo from "./OrderInfo";
import Trajectory from "../map/Trajectory";

const { confirm } = Modal;
const { Option } = Select;
const id = localStorage.getItem("userid");
const token = localStorage.getItem("token");
const address_id = localStorage.getItem("address_id");
function OrderList() {
  const [day, setDay] = useState(1);
  const [reason, setReason] = useState();

  const [temp, setTemp] = useState({});

  const [modalData, setModalData] = useState({});
  const [o_id, setO_id] = useState();

  const [infoVisible, setInfoVisible] = useState(false);

  const [visible, setvisible] = useState(false);
  const [mapVisible, setMapVisible] = useState(false);

  const [loading, setLoading] = useState(true);

  const [tableData, settableData] = useState({});
  const [postData, setpostData] = useState({
    id,
    token,
    address_id,
    page: 1
  });
  const initData = {
    id,
    token,
    address_id,
    page: 1,
    search: "",
    state: "",
    sear_time_s: "",
    sear_time_e: ""
  };
  useEffect(() => {
    getUser(initData);
    console.log(tableData);

    return () => {};
  }, []);
  async function reset() {
    setpostData(initData);
    setTemp({})
    getUser(initData);
  }
  async function onPageChange(e) {
    setpostData({ ...postData, page: e });
    getUser({ ...postData, page: e });
  }
  async function exportExcel() {
    let { state, search } = postData;
    let start_time = postData.sear_time_s;
    let end_time = postData.sear_time_e
		
		if (state == '' && search!='' && start_time!=''&& end_time!='') {
			window.location.href = baseURL + '/admin/Exportdata/orderexporde/start_time/' + start_time + '/' + 'end_time/' + end_time + '/' + 'phone/' + search+'/address_id/' + address_id;
		}
		if(search=='' && state != '' && start_time!=''&& end_time!=''){
			window.location.href = baseURL + '/admin/Exportdata/orderexporde/state/' + state + '/' +
				'start_time/' + start_time + '/' + 'end_time/' + end_time+'/address_id/' + address_id;
		}
		if(start_time==''&& search!='' && state != ''&& end_time!=''){
				window.location.href = baseURL + '/admin/Exportdata/orderexporde/state/' + state + '/'+ 'end_time/' + end_time + '/' + 'phone/' + search+'/address_id/' + address_id;
		}
		if(end_time=='' && search!='' && state != '' && start_time!=''){
			window.location.href = baseURL + '/admin/Exportdata/orderexporde/state/' + state + '/' +
				'start_time/' + start_time + '/'+ 'phone/' + search+'/address_id/' + address_id;
		}
		if(state == '' && search==''&& start_time != '' && end_time!=''){
			window.location.href = baseURL + '/admin/Exportdata/orderexporde/start_time/'+start_time+'/'+'end_time/'+end_time+'/address_id/' + address_id;
		}
		if(state == '' && start_time==''&& search != '' && end_time!=''){
			window.location.href = baseURL + '/admin/Exportdata/orderexporde/end_time/' + end_time + '/' + 'phone/' + search+'/address_id/' + address_id;
		}
		if(state == '' && end_time==''&& search != '' && start_time!=''){
			window.location.href = baseURL + '/admin/Exportdata/orderexporde/start_time/' + start_time + '/' + 'phone/' + search+'/address_id/' + address_id;
		}
		if(search == '' && start_time=='' && state != '' && end_time!=''){
			window.location.href = baseURL + '/admin/Exportdata/orderexporde/state/' + state + '/'+ 'end_time/' + end_time+'/address_id/' + address_id;
		}
		if(search == '' && end_time=='' && state != '' && start_time!=''){
			window.location.href = baseURL + '/admin/Exportdata/orderexporde/state/' + state + '/' +'start_time/' + start_time+'/address_id/' + address_id;
		}
		if(start_time == '' && end_time=='' && search != '' && state!=''){
			window.location.href = baseURL + '/admin/Exportdata/orderexporde/state/' + state + '/' + 'phone/' + search+'/address_id/' + address_id;
		}
		if(start_time == '' && end_time=='' && search==''){
			window.location.href = baseURL + '/admin/Exportdata/orderexporde/state/' + state+'/address_id/' + address_id;
		}
		if(state == '' && end_time=='' && start_time==''){
			window.location.href = baseURL + '/admin/Exportdata/orderexporde/phone/' + search+'/address_id/' + address_id;
		}
		if(end_time == '' && state=='' && search==''){
			window.location.href = baseURL + '/admin/Exportdata/orderexporde/start_time/' + start_time+'/address_id/' + address_id;
		}
		if(state == '' && search=='' && start_time==''){
			window.location.href = baseURL + '/admin/Exportdata/orderexporde/end_time/' + end_time+'/address_id/' + address_id;
		}
		if(end_time!='' && search!='' && state != '' && start_time!=''){
			window.location.href = baseURL + '/admin/Exportdata/orderexporde/state/' + state + '/' +
				'start_time/' + start_time + '/' + 'end_time/' + end_time + '/' + 'phone/' + search+'/address_id/' + address_id;
		}
		if(end_time=='' && search=='' && state == '' && start_time==''){
			window.location.href = baseURL + '/admin/Exportdata/orderexporde/address_id/' + address_id;
		}
	
  }
  async function getUser(Data = { ...postData }) {
    setLoading(true);
    console.log("Data", Data);
    try {
      const response = await Api.post(
        "/admin/Order/orderlist",

        qs.stringify(Data)
      );
      console.log(response);
      response.data.error_code === 0 && settableData(response.data);
      // if (response.data.error_code == -101) {
      // 	window.location.href = '../login/login.html';
      // }
      setLoading(false);
    } catch (error) {
      console.error(error);
    }
  }
  async function handleChange(d_id, operation, address_id) {
    setLoading(true);

    let data = { d_id, operation, address_id };
    try {
      const response = await Api.post(
        "/admin/Driver/up_operation",

        qs.stringify(data)
      );
      console.log(response);
      getUser(initData);

      message.success("操作成功");
    } catch (error) {
      message.success("操作失败");
    }
  }
  function searchChange() {
    getUser({ ...postData, page: 1 });
  }
  async function cancel(text, o_id) {
    let data = {
      id,
      token,
      text,
      o_id,
      address_id
    };
    try {
      const response = await Api.post(
        "/admin/Order/cancelorder",
        qs.stringify(data)
      );
      console.log(response);
      getUser();
      setvisible(false);
      response.data.error_code === 0
      ? message.success("操作成功")
      : message.warning(response.data.message);
    } catch (error) {
      message.success("操作失败");
    }
  }
  async function showMap(record){
    let data = { id, token, o_id: record.o_id };
    try {
      const response = await Api.post(
        "/admin/Order/order_trajectory",

        qs.stringify(data)
      );
      console.log("轨迹", response);
      let list =response.data

      if(list ===null){
      message.warning("轨迹里程太短");

      }
      else{
        setModalData(list)
        setMapVisible(true)
   
      }
   

    } catch (error) {
    }

  }

  async function detail(d_id) {
    setLoading(true);

    // try {
    //   const response = await Api.post(
    //     "/admin/Driver/up_operation",

    //     qs.stringify(data)
    //   );
    //   console.log(response);
    //   getUser(initData);

    //   message.success("操作成功");
    // } catch (error) {
    //   message.success("操作失败");
    // }
  }

  const columns = [
    {
      title: "订单编号",
      dataIndex: "order_num",
      key: "order_num"
    },
    {
      title: "司机姓名",
      dataIndex: "d_name",
      key: "d_name"
    },
    {
      title: "司机电话",
      dataIndex: "d_phone",
      key: "d_phone"
    },
    {
      title: "用户电话",
      dataIndex: "u_phone",
      key: "u_phone"
    },
    {
      title: "金额",
      dataIndex: "amount",
      key: "amount"
    },
    {
      title: "状态",
      dataIndex: "state_cn",
      key: "state_cn"
    },

    {
      title: "起点",
      dataIndex: "saddress",
      key: "saddress"
    },
    {
      title: "终点",
      dataIndex: "eaddress",
      key: "eaddress"
    },
    {
      title: "支付方式",
      dataIndex: "paymethod",
      key: "paymethod"
    },
    {
      title: "订单开始时间",
      dataIndex: "sdate",
      key: "sdate"
    },

    {
      fixed: "right",
      title: "操作",
      key: "action",
      render: (text, record) => (
        <div>
          <Button
            size="small"
            onClick={() => {
              setO_id(record.o_id);
              setInfoVisible(true);
            }}
          >
            详情
          </Button>
          <Button
            ghost
            size="small"
            onClick={() => {
              setvisible(true);
              setO_id(record.o_id);
            }}
            type="danger"
          >
            取消
          </Button>
          <Button
            size="small"
            onClick={() => {
              showMap(record)
            }
             }
            ghost
            type="primary"
          >
            轨迹
          </Button>
        </div>
      )
    }
  ];

  return (
    <div>
      <Card>
        <Row>
          <Col span={6}>
            <Input
              value={postData.search}
              placeholder="请输入订单编号，用户手机号或司机手机号"
              onChange={e =>
                setpostData({ ...postData, search: e.target.value })
              }
            />
          </Col>
          <Col span={3} offset={1}>
            <Select
              style={{ width: "100%" }}
              value={postData.state}
              defaultValue="default"
              placeholder="订单状态"
              onChange={e => {
                console.log("e", e);
                setpostData({ ...postData, state: e });
              }}
            >
              <Option value="">全部</Option>
              <Option value="new">未出发</Option>
              <Option value="on">新订单</Option>
              <Option value="active">行程中</Option>
              <Option value="wait_pay">待支付</Option>
              <Option value="end">已结束</Option>
              <Option value="cannal">已取消</Option>
            </Select>
          </Col>
          开始时间：
          <DatePicker
          value={temp.sear_time_s}
            onChange={(e, dateString) => {
              console.log("dateString", dateString);
              setpostData({ ...postData, sear_time_s: dateString });
              setTemp({...temp,sear_time_s:e});

            }}
          />
          结束时间：
          <DatePicker
              value={temp.sear_time_e}
            onChange={(e, dateString) => {
              console.log("dateString", dateString);
              setpostData({ ...postData, sear_time_e: dateString });
              setTemp({...temp,sear_time_e:e});

            }}
          />
          <Button onClick={() => searchChange()}>搜索</Button>
          <Button onClick={reset}>重置</Button>
          <Button onClick={exportExcel}>导出</Button>
        </Row>
      </Card>
      <Card>
        <Table
          scroll={{ x: "130%" }}
          columns={columns}
          loading={loading}
          dataSource={tableData.list}
          pagination={{
            current: postData.page,
            total: Number(tableData.count),
            onChange: e => onPageChange(e),
            disabled: loading,
            showQuickJumper: true,
            showTotal: total => `共${total}条`
          }}
        />
      </Card>
      <Modal
        destroyOnClose={true}
        width="80%"
        title="轨迹查看"
        visible={infoVisible}
        onCancel={() => setInfoVisible(false)}
        footer={[
          <Button key="back" onClick={() => setInfoVisible(false)}>
            关闭
          </Button>
          // <Button key="submit" type="primary" loading={loading} onClick={this.handleOk}>
          //   Submit
          // </Button>,
        ]}
      >
        <OrderInfo id={o_id}></OrderInfo>
      </Modal>
      <Modal
        title="请填写取消订单的原因?"
        visible={visible}
        onOk={() => cancel( reason,o_id)}
        onCancel={() => setvisible(false)}
      >
        <Input onChange={(e)=>{setReason(e.target.value)}}></Input>
      </Modal>
      <Modal
        destroyOnClose={true}
        width="80%"
        title="司机信息"
        visible={mapVisible}
        onCancel={() => setMapVisible(false)}
        footer={[
          <Button key="back" onClick={() => setMapVisible(false)}>
            关闭
          </Button>
          // <Button key="submit" type="primary" loading={loading} onClick={this.handleOk}>
          //   Submit
          // </Button>,
        ]}
      >
        <Trajectory data={modalData} id={o_id}></Trajectory>
      </Modal>
    </div>
  );
}

export default OrderList;
