import React, { useState, useEffect } from "react";
import {
  Table,
  Button,
  Select,
  Card,
  Input,
  message,
  Row,
  Col,
  Modal,
  Divider,
  InputNumber,
  Form
} from "antd";
import { Api, baseURL } from "../api";
import qs from "qs";
import { Link } from "react-router-dom";
import Axios from "axios";
const { confirm } = Modal;
const { Option } = Select;
const id = localStorage.getItem("userid");
const token = localStorage.getItem("token");
const address_id = localStorage.getItem("address_id");
function DriverInfo(props) {

  const [loading, setLoading] = useState(true);

  const [tableData, settableData] = useState({ list: [] });
  const [List, setList] = useState([]);
  const [postData, setpostData] = useState({
    id,
    token,
    address_id,
    page: 1
  });
  const initData = {
    id,
    token,
    address_id
  };

  useEffect(() => {
    getUser(initData);
    console.log(tableData);
    return () => {};
  }, []);

  async function getUser(Data = { ...postData }) {
    setLoading(true);
    console.log("Data", Data);
    try {
      const response = await Api.post(
        "/admin/Setting/userreward_get",

        qs.stringify(Data)
      );
      console.log(response);
      response.data.error_code === 0 && setList(response.data.list);

      setLoading(false);
    } catch (error) {
      console.error(error);
    }
  }
  async function save() {
    setLoading(true);

    let data = { id, token, address_id, setArray:List };
    try {
   
      const response = await Api.post(
        "/admin/Setting/assign_driver_set",

        qs.stringify(data)
      );
      console.log('2',response);

      response.data.error_code === 0
        ? message.success("操作成功")
        : message.warning(response.data.message);
      setLoading(false);
    } catch (error) {
      message.success("操作失败");
      setLoading(false);
    }
  }
  async function inputChange(e, index, key) {
    console.log(e, index, key);
    let temp = JSON.parse(JSON.stringify(List));
    console.log(temp);

    temp[index][key] = String(e);
    console.log(temp);

    setList(temp);
  }
  return (
    <div>
      <Card>
        {List.map((item, index) => (
          <div>
            <h1>{item.area_name}</h1>
            <Form.Item>
              <Row>
                <Col span={6}>免费秒数</Col>
                <Col span={6}>
                  <InputNumber
                       parser={value => {
                        if (parseInt(value) < 0) {
                            return 0;
                          }
                          return value;
                        }}
                    defaultValue={item.free_time}
                    min={0}
                    onChange={e => inputChange(e, index, "free_time")}

                  ></InputNumber>
                </Col>
                <Col span={6}>超时奖励秒数段</Col>
                <Col span={6}>
                  <InputNumber
                       parser={value => {
                        if (parseInt(value) < 0) {
                            return 0;
                          }
                          return value;
                        }}
                    defaultValue={item.cost_every}
                    onChange={e => inputChange(e, index, "cost_every")}

                  ></InputNumber>
                </Col>
              </Row>
            </Form.Item>
            <Form.Item>
              <Row>
                <Col span={6}>每时段奖励金</Col>
                <Col span={6}>
                  <InputNumber
                       parser={value => {
                        if (parseInt(value) < 0) {
                            return 0;
                          }
                          return value;
                        }}
                    step={0.1}
                    defaultValue={item.every_amount}
                    precision={1}
                    onChange={e => inputChange(e, index, "every_amount")}

                  ></InputNumber>
                </Col>

                <Col span={6}>最高限制奖励金</Col>
                <Col span={6}>
                  <InputNumber
                       parser={value => {
                        if (parseInt(value) < 0) {
                            return 0;
                          }
                          return value;
                        }}
                    
                    defaultValue={item.limit_amount}
                    onChange={e => inputChange(e, index, "limit_amount")}
                  ></InputNumber>
                </Col>
              </Row>
            </Form.Item>
       
          </div>
        ))}
        <div style={{ display: "flex", justifyContent: "right" }}>
          <Button
            disabled={loading}
            onClick={save}
            size={"large"}
            type={"primary"}
          >
            立刻设置
          </Button>
        </div>
      </Card>
    </div>
  );
}

export default DriverInfo;
