import React, { useState, useEffect } from "react";
import {
  Table,
  Button,
  Select,
  Card,
  Input,
  message,
  Row,
  Col,
  Modal,
  DatePicker
} from "antd";
import { Api, baseURL } from "../api";
import qs from "qs";
import { Link } from "react-router-dom";
import DriverInfo from "../driver/DriverInfo";
const { confirm } = Modal;
const { Option } = Select;
const id = localStorage.getItem("userid");
const token = localStorage.getItem("token");
const address_id = localStorage.getItem("address_id");
function DriverList() {
  const [day, setDay] = useState(1);
  const [d_id, setD_id] = useState();
  const [infoVisible, setInfoVisible] = useState(false);
  const [temp, setTemp] = useState({});

  const [visible, setvisible] = useState(false);
  const [loading, setLoading] = useState(true);

  const [tableData, settableData] = useState({});
  const [postData, setpostData] = useState({
    id,
    token,
    address_id,
    page: 1
  });
  const initData = {
    id,
    token,
    address_id,
    page: 1,
    search: "",
    reviewed: "",
    state: ""
  };

  useEffect(() => {
    getUser(initData);
    console.log(tableData);

    return () => {};
  }, []);
  async function reset() {
    setpostData(initData);
    getUser(initData);
  }
  async function onPageChange(e) {
    setpostData({ ...postData, page: e });
    getUser({ ...postData, page: e });
  }
  async function exportExcel() {
    let { state, reviewed } = postData;
    if (state == "" && reviewed != "") {
      window.location.href =
        baseURL +
        "/admin/Exportdata/driverlistexporde/reviewed/" +
        reviewed +
        "/address_id/" +
        address_id;
    }
    if (reviewed == "" && state != "") {
      window.location.href =
        baseURL +
        "/admin/Exportdata/driverlistexporde/state/" +
        state +
        "/address_id/" +
        address_id;
    }
    if (state != "" && reviewed != "") {
      window.location.href =
        baseURL +
        "/admin/Exportdata/driverlistexporde/state/" +
        state +
        "/reviewed/" +
        reviewed +
        "/address_id/" +
        address_id;
    }
    if (state == "" && reviewed == "") {
      window.location.href =
        baseURL +
        "/admin/Exportdata/driverlistexporde/address_id/" +
        address_id;
    }
  }
  async function getUser(Data = { ...postData }) {
    setLoading(true);
    console.log("Data", Data);
    try {
      const response = await Api.post(
        "/admin/User/index",

        qs.stringify(Data)
      );
      console.log(response);
      response.data.error_code === 0 && settableData(response.data);
      // if (response.data.error_code == -101) {
      // 	window.location.href = '../login/login.html';
      // }
      setLoading(false);
    } catch (error) {
      console.error(error);
    }
  }
  async function handleChange(d_id, operation, address_id) {
    setLoading(true);

    let data = { d_id, operation, address_id };
    try {
      const response = await Api.post(
        "/admin/Driver/up_operation",

        qs.stringify(data)
      );
      console.log(response);
      getUser(initData);

      message.success("操作成功");
    } catch (error) {
      message.success("操作失败");
    }
  }
  function searchChange() {
    getUser({ ...postData, page: 1 });
  }
  async function Banned(day, d_id) {
    let data = {
      id,
      token,
      day,
      d_id,
      address_id
    };
    try {
      const response = await Api.post(
        "/admin/Driver/banned_driver",
        qs.stringify(data)
      );
      console.log(response);
      getUser();
      setvisible(false);
      message.success("操作成功");
    } catch (error) {
      message.success("操作失败");
    }
  }
  async function unblock(record) {
    console.log(record);
    let data = { id, token, d_id: record.d_id, address_id };

    if (record.available_pd == "Y") {
      message.info("状态正常");
      return false;
    } else {
      confirm({
        title: "确认解封?",
        content: "",
        async onOk() {
          try {
            const response = await Api.post(
              "/admin/Driver/unbanned_driver",

              qs.stringify(data)
            );
            console.log("解封", response);
            getUser();

            message.success("操作成功");
          } catch (error) {
            message.success("操作失败");
          }
        },
        onCancel() {}
      });
    }
  }
  async function detail(d_id) {
    setLoading(true);

    // try {
    //   const response = await Api.post(
    //     "/admin/Driver/up_operation",

    //     qs.stringify(data)
    //   );
    //   console.log(response);
    //   getUser(initData);

    //   message.success("操作成功");
    // } catch (error) {
    //   message.success("操作失败");
    // }
  }

  const columns = [
    {
      title: "头像",
      key: "headimgurl",

      render: (text, record) => (
        <img
          style={{ width: "100px", height: "100px" }}
          src={record.headimgurl}
        ></img>
      )
    },
    {
      title: "用户昵称",
      dataIndex: "nickname",
      key: "nickname"
    },
    {
      title: "用户电话",
      dataIndex: "phone",
      key: "phone"
    },

    {
      title: "用车状态",
      dataIndex: "state_cn",
      key: "state_cn"
    },
    {
      title: "已使用/未使用",
      dataIndex: "coup_nums_y",
      key: "coup_nums_y"
    },

    {
      title: "注册时间",
      dataIndex: "sdate",
      key: "sdate"
    }
  ];

  return (
    <div>
      <Card>
        <Row gutter={16}>
          <Col span={3} >
            <Input
              value={postData.search}
              placeholder="请输入用户昵称"
              onChange={e =>
                setpostData({ ...postData, search: e.target.value })
              }
            />
          </Col>

          <Col span={3} offset={1}>
            <Select
              style={{ width: "100%" }}
              value={postData.state}
              defaultValue="default"
              placeholder="司机状态"
              onChange={e => {
                console.log("e", e);
                setpostData({ ...postData, state: e });
              }}
            >
              <Option value="">全部</Option>
              <Option value="Y">行程中</Option>
              <Option value="N">未用车</Option>
              <Option value="O">等待派车</Option>
            </Select>
          </Col>
          <Col span={3} offset={1}>
            <Select
              style={{ width: "100%" }}
              defaultValue="default"
              value={postData.attend}
              placeholder="用户注册状态"
              onChange={e => {
                console.log("e", e);
                setpostData({ ...postData, attend: e });
              }}
            >
              <Option value="">全部</Option>
              <Option value="Y">已注册</Option>
              <Option value="N">未注册</Option>
            </Select>
          </Col>
          <Col span={3} offset={1}>
            <Select
              style={{ width: "100%" }}
              defaultValue="default"
              value={postData.is_order}
              placeholder="是否打过车"
              onChange={e => {
                console.log("e", e);
                setpostData({ ...postData, is_order: e });
              }}
            >
              <Option value="">全部</Option>
              <Option value="Y">打过车</Option>
              <Option value="N">未打过车</Option>
            </Select>
          </Col>
        </Row>
        <br></br>
        <Row gutter={16}>
          <Col span={6}>
            开始时间：
            <DatePicker
              value={temp.sear_time_s}
              onChange={(e, dateString) => {
                console.log("dateString", dateString);
                setpostData({ ...postData, sear_time_s: dateString });
                setTemp({ ...temp, sear_time_s: e });
              }}
            />
          </Col>
          <Col span={6}>
            结束时间：
            <DatePicker
              value={temp.sear_time_e}
              onChange={(e, dateString) => {
                console.log("dateString", dateString);
                setpostData({ ...postData, sear_time_e: dateString });
                setTemp({ ...temp, sear_time_e: e });
              }}
            />
          </Col>
          <Col span={2}>
            <Button onClick={() => searchChange()}>搜索</Button>
          </Col>
          <Col span={2}>
            <Button onClick={reset}>重置</Button>
          </Col>
          <Col span={2}>
            <Button onClick={exportExcel}>导出</Button>
          </Col>
        </Row>
      </Card>
      <Card>
        <Table
          columns={columns}
          loading={loading}
          dataSource={tableData.list}
          pagination={{
            current: postData.page,
            total: Number(tableData.count),
            onChange: e => onPageChange(e),
            disabled: loading,
            showQuickJumper: true,
            showTotal: total => `共${total}条`
          }}
        />
      </Card>
      <Modal
        destroyOnClose={true}
        width="80%"
        title="司机信息"
        visible={infoVisible}
        onCancel={() => setInfoVisible(false)}
        footer={[
          <Button key="back" onClick={() => setInfoVisible(false)}>
            关闭
          </Button>
          // <Button key="submit" type="primary" loading={loading} onClick={this.handleOk}>
          //   Submit
          // </Button>,
        ]}
      >
        <DriverInfo d_id={d_id}></DriverInfo>
      </Modal>
      <Modal
        title="确认封禁?"
        visible={visible}
        onOk={() => Banned(day, d_id)}
        onCancel={() => setvisible(false)}
      >
        <Select style={{ width: "100%" }} onChange={e => setDay(e)}>
          <Option value="1">1天</Option>
          <Option value="2">2天</Option>
          <Option value="3">3天</Option>
          <Option value="7">7天</Option>
          <Option value="15">15天</Option>
          <Option value="forever">永久</Option>
        </Select>
      </Modal>
    </div>
  );
}

export default DriverList;
