import React, { useState } from "react";
import logo from "./logo.svg";
import "./App.css";
import { HashRouter as Router, Route, Link,Switch } from "react-router-dom";
import DriverList from "./driver/DriverList";
import DriverAudit from "./driver/DriverAudit";
import DriverAvatar from "./driver/DriverAvatar";
import DriverWithdraw from "./driver/DriverWithdraw";

import ComplaintList from "./driver/ComplaintList";


import OrderList from "./order/OrderList";


import OrderFlow from "./driver/OrderFlow";
import TotalFlow from "./driver/TotalFlow";

//红包
import CouponList from "./coupon/CouponList";
import CouponSendList from "./coupon/CouponSendList";
import CouponSet from "./coupon/CouponSet";

import UserList from "./driver/UserList";

import RewardList from "./driver/RewardList";

import FareSet from "./setting/FareSet";
import OrderSet from "./setting/OrderSet";
import RangeSet from "./setting/RangeSet";

import WaitSet from "./setting/WaitSet";

import TabPage from "./common/TabPage"


import InvitCount from "./driver/InvitCount";
import Trajectory from "./map/Trajectory";

import Login from "./login/Login";

import { ConfigProvider } from "antd";
import zhCN from "antd/es/locale/zh_CN";
import { Input, Button,message } from "antd";
import { Api } from "./api";
import qs from "qs";
//司机在岗统计
import DriverOnDuty from "./driver/DriverOnDuty";
function App(props) {
  console.log(props)
  const [loading, setLoading] = useState(false);

  const [postData, setpostData] = useState({
    account: null,
    password: null
  });
  async function login() {
    setLoading(true);
    console.log("postData", postData);

    let data = { ...postData };
    try {
      const response = await Api.post(
        "/admin/Index/login",

        qs.stringify(data)
      );
      console.log(response);
      if (response.data.error_code == 0) {
        let account=data.account;
        let userid=data.id;
        let token=data.token;
        let address_id=data.address_id;
        localStorage.setItem("account",account)
        localStorage.setItem("userid",userid)
        localStorage.setItem("token",token)
        localStorage.setItem("address_id",address_id)
        
      } else if (data.error_code == -403) {
        alert('账号密码错误');
      }
      setLoading(false);
    } catch (error) {
      console.error(error);
    }
  }
  return (
    <ConfigProvider locale={zhCN}>

<Router>
      <div>
        {/* <nav>
          <ul>
            <li>
              <Link to="/">zhuc</Link>
            </li>
            <li>
              <Link to="/driverlist">司机列表</Link>
            </li>
           
          </ul>
        </nav> */}

        {/* A <Switch> looks through its children <Route>s and
            renders the first one that matches the current URL. */}
        <Switch>
          <Route path="/driverlist">
            <DriverList />
          </Route>
          <Route path="/driveraudit">
            <DriverAudit />
          </Route>
          <Route path="/driveravatar">
            <DriverAvatar />
          </Route>
          <Route path="/driverwithdraw">
            <DriverWithdraw />
          </Route>
          //司机在岗时间
          <Route path="/driveronduty">
            <DriverOnDuty />
          </Route>
          <Route path="/invitcount/:driverId">
            <InvitCount />
          </Route>
          <Route path="/driverinfo/:driverId">
            <InvitCount />
          </Route>
          <Route path="/complaint">
            <ComplaintList />
          </Route>
          <Route path="/orderlist">
            <OrderList />
          </Route>
          <Route path="/orderflow">
            <OrderFlow />
          </Route>
          <Route path="/totalflow">
            <TotalFlow />
          </Route>
          <Route path="/couponset">
            <CouponSet />
          </Route>
          <Route path="/couponsendlist">
            <CouponSendList />
          </Route>
          <Route path="/couponlist">
            <CouponList />
          </Route>
          <Route path="/userlist">
            <UserList />
          </Route>


          <Route path="/orderset">
            <OrderSet/>
          </Route>
          <Route path="/fareset">
            <TabPage id="FareSet"/>
          </Route>
          <Route path="/rangeset">
            <FareSet/>
          </Route>
          <Route path="/waitset">
            <WaitSet/>
          </Route>


          <Route path="/rewardlist">
            <RewardList />
          </Route>
          <Route path="/trajectory">
            <RewardList />
          </Route>
          <Route path="/login">
            <Login />
          </Route>
          <Route path="/">
            <Login />
          </Route>
        </Switch>
      </div>
    </Router>
    </ConfigProvider>
  );
}

export default App;
