import React, { useState, useEffect } from "react";
import {
  Table,
  Button,
  Select,
  Card,
  Input,
  message,
  Row,
  Col,
  Modal,
  Divider,
  InputNumber,
  Form,
  Tabs
} from "antd";
import { Api, baseURL } from "../api";
import qs from "qs";
import { Link } from "react-router-dom";
import Axios from "axios";
import FareSet from "../setting/FareSet";

const { TabPane } = Tabs;
const { confirm } = Modal;
const { Option } = Select;
const id = localStorage.getItem("userid");
const token = localStorage.getItem("token");
const address_id = localStorage.getItem("address_id");
function TabPage(props) {
  const pageId= props.id

  const [day, setDay] = useState(1);
  const [visible, setvisible] = useState(false);
  const [infoVisible, setInfoVisible] = useState(false);

  const [loading, setLoading] = useState(true);

  const [tableData, settableData] = useState({ list: [] });
  const [List, setList] = useState([]);
  const [postData, setpostData] = useState({
    id,
    token,
  });
  const initData = {
    id,
    token,
    address_id
  };

  useEffect(() => {
    getArea(initData);
    console.log(tableData);
    return () => {};
  }, []);

  async function getArea(Data = { ...postData }) {
    setLoading(true);
    console.log("Data", Data);
    try {
      const response = await Api.post(
        "/admin/Index/getadmin_area",
        qs.stringify(Data)
      );
      console.log(response);
      response.data.error_code === 0 && setList(response.data.list);

      setLoading(false);
    } catch (error) {
      console.error(error);
    }
  }

  return (
    <div>
      <Card>
        <Tabs defaultActiveKey="1">
        {List.map((item, index) => (
            <TabPane tab={item.area_name} key={index}>
              {pageId==='FareSet'? <FareSet id={item.address_id}/>:''}
           
          </TabPane>
        ))}
        </Tabs>
 
      </Card>
    </div>
  );
}

export default TabPage;
