import React, { useState, useEffect } from "react";
import {
  Table,
  Button,
  Select,
  Card,
  Input,
  message,
  Row,
  Col,
  Modal,
  Divider,
  InputNumber,
  Form,
  Switch
} from "antd";
import { Api, baseURL } from "../api";
import qs from "qs";
import { Link } from "react-router-dom";
import Axios from "axios";
const { confirm } = Modal;
const { Option } = Select;
const id = localStorage.getItem("userid");
const token = localStorage.getItem("token");
const address_id = localStorage.getItem("address_id");
function DriverInfo(props) {
  const [day, setDay] = useState(1);
  const [visible, setvisible] = useState(false);
  const [infoVisible, setInfoVisible] = useState(false);

  const [loading, setLoading] = useState(true);

  const [tableData, settableData] = useState({ list: [] });
  const [List, setList] = useState([1]);
  const [postData, setpostData] = useState({
    id,
    token,
    address_id,
    page: 1
  });
  const initData = {
    id,
    token,
    address_id
  };

  useEffect(() => {
    getUser(initData);
    console.log(tableData);
    return () => {};
  }, []);

  async function getUser(Data = { ...postData }) {
    setLoading(true);
    console.log("Data", Data);
    try {
      const response = await Api.post(
        "/Admin/Coup/incentive_info",

        qs.stringify(Data)
      );
      console.log(response);
      response.data.error_code === 0 && setList(response.data.list);

      setLoading(false);
    } catch (error) {
      console.error(error);
    }
  }
  async function save() {
    setLoading(true);

    let data = { id, token, address_id, setArray:List };
    try {
   
      const response = await Api.post(
        "/Admin/Coup/driver_incentive",

        qs.stringify(data)
      );
      console.log('2',response);

      response.data.error_code === 0
        ? message.success("操作成功")
        : message.success("操作失败");
      setLoading(false);
    } catch (error) {
      message.success("操作失败");
      setLoading(false);
    }
  }
  async function inputChange(e, index, key) {
    console.log(e, index, key);
    let temp = JSON.parse(JSON.stringify(List));
    console.log(temp);

    temp[index][key] = String(e);
    console.log(temp);

    setList(temp);
  }
  return (
    <div>
      <Card>
        {List.map((item, index) => (
          <div>
            <h1>{item.area_name}</h1>
            <Form.Item>
              <Row>
                <Col span={4}>红包金额</Col>
                <Col span={6}>
                  <InputNumber
                    step={0.01}
                    defaultValue={item.mon_fee}
                    precision={2}
                    onChange={e => inputChange(e, index, "mon_fee")}

                  ></InputNumber>
                </Col>
                <Col span={6}>邀请司机获得奖励</Col>
                <Col span={6}>
                  <InputNumber
                  
                    step={0.01}
                    defaultValue={item.invite_driver_reward}
                    precision={2}
                    onChange={e => inputChange(e, index, "invite_driver_reward")}

                  ></InputNumber>
                </Col>
                <Col span={2}>
                <Switch defaultChecked onChange={inputChange} />

                </Col>
              </Row>
            </Form.Item>
          
          </div>
        ))}
        <div style={{ display: "flex", justifyContent: "right" }}>
          <Button
            disabled={loading}
            onClick={save}
            size={"large"}
            type={"primary"}
          >
            立刻设置
          </Button>
        </div>
      </Card>
    </div>
  );
}

export default DriverInfo;
