import React, { useState, useEffect } from "react";
import {
  Table,
  Button,
  Select,
  Card,
  Input,
  message,
  Row,
  Col,
  Modal,
  Divider
} from "antd";
import { Api } from "../api";
import qs from "qs";
import { Link } from "react-router-dom";
import startIcon from "../assets/img/str.png";
import endIcon from "../assets/img/end.png";

const { confirm } = Modal;
const { Option } = Select;
const id = localStorage.getItem("userid");
const token = localStorage.getItem("token");
const address_id = localStorage.getItem("address_id");
function DriverInfo(props) {
  const [loading, setLoading] = useState(true);

  const [map, setmap] = useState({});
  const [postData, setpostData] = useState({
    id,
    token,
    address_id,
    page: 1
  });
  const initData = {
    id,
    token,
    o_id: props.id
  };
  let BMap = window.BMap;
  const list = props.data;
  const listLast = list.length - 1;

  let myBeginIcon = new BMap.Icon(startIcon, new BMap.Size(28, 28), {
    imageOffset: new BMap.Size(0, 0)
  }); //起点
  let myEndIcon = new BMap.Icon(endIcon, new BMap.Size(28, 28), {
    imageOffset: new BMap.Size(0, 0)
  }); //终点
  useEffect(() => {
    if (props.data.length >= 1) {
      console.log("有数据");
      const map = new BMap.Map("map"); // 创建Map实例
      setmap(map);

      map.centerAndZoom(new BMap.Point(list[0].Long, list[0].Lat), 16); // 初始化地图,设置中心点坐标和地图级别
      map.addControl(new BMap.MapTypeControl()); //添加地图类型控件
      map.enableScrollWheelZoom(true); //开启鼠标滚轮缩放
      let start = new BMap.Marker(
        new BMap.Point(list[0].Long, list[0].Lat), //起始点的经纬度
        {
          icon: myBeginIcon
        }
      );
      map.addOverlay(start);
      let end = new BMap.Marker(
        new BMap.Point(list[listLast].Long, list[listLast].Lat), //终点的经纬度
        {
          icon: myEndIcon
        }
      );
      console.log('location start',listLast,list[0].Long,list[0].Lat)

      console.log('location end',listLast,list[listLast].Long,list[listLast].Lat)

      map.addOverlay(end);

      for (var i = 0; i < list.length - 1; i++) {
        const polyline = new BMap.Polyline(
          [
            new BMap.Point(list[i].Long, list[i].Lat), //起始点的经纬度
            new BMap.Point(list[i + 1].Long, list[i + 1].Lat) //终点的经纬度
          ],
          {
            strokeColor: "green", //设置颜色
            strokeWeight: 4, //宽度
            strokeOpacity: 1
          }
        ); //透明度

        map.addOverlay(polyline);
      }
    
    }

    return () => {};
  }, [props]);

  async function getUser(Data = { ...postData }) {}
  async function handleChange(d_id, operation, address_id) {
    setLoading(true);

    let data = { d_id, operation, address_id };
    try {
      const response = await Api.post(
        "/admin/Driver/up_operation",

        qs.stringify(data)
      );
      console.log(response);
      getUser(initData);

      message.success("操作成功");
    } catch (error) {
      message.success("操作失败");
    }
  }
  function searchChange(d_id) {
    console.log("搜索按钮");
  }
  async function Banned(day, d_id) {
    let data = {
      id,
      token,
      day,
      d_id,
      address_id
    };
    try {
      const response = await Api.post(
        "/admin/Driver/banned_driver",
        qs.stringify(data)
      );
      console.log(response);
      getUser();
      message.success("操作成功");
    } catch (error) {
      message.success("操作失败");
    }
  }
  async function unblock(record) {
    console.log(record);
    let data = { id, token, d_id: record.d_id, address_id };

    if (record.available_pd == "Y") {
      message.info("状态正常");
      return false;
    } else {
      confirm({
        title: "确认解封?",
        content: "",
        async onOk() {
          try {
            const response = await Api.post(
              "/admin/Driver/unbanned_driver",

              qs.stringify(data)
            );
            console.log("解封", response);
            getUser();

            message.success("操作成功");
          } catch (error) {
            message.success("操作失败");
          }
        },
        onCancel() {}
      });
    }
  }
  async function detail(d_id) {
    setLoading(true);

    // try {
    //   const response = await Api.post(
    //     "/admin/Driver/up_operation",

    //     qs.stringify(data)
    //   );
    //   console.log(response);
    //   getUser(initData);

    //   message.success("操作成功");
    // } catch (error) {
    //   message.success("操作失败");
    // }
  }

  return (
    <div>
      <div id="map" style={{ height: "60vh" }}></div>
    </div>
  );
}

export default DriverInfo;
