import React, { useState, useEffect } from "react";
import {
  Table,
  Button,
  Select,
  Card,
  Input,
  message,
  Row,
  Col,
  Modal
} from "antd";
import { Api, baseURL } from "../api";
import qs from "qs";
import { Link } from "react-router-dom";
import DriverInfo from './DriverInfo'
const { confirm } = Modal;
const { Option } = Select;
const id = localStorage.getItem("userid");
const token = localStorage.getItem("token");
const address_id = localStorage.getItem("address_id");
function DriverList() {
  const [day, setDay] = useState(1);
  const [d_id, setD_id] = useState();
  const [infoVisible, setInfoVisible] = useState(false);

  const [visible, setvisible] = useState(false);
  const [loading, setLoading] = useState(true);

  const [tableData, settableData] = useState({});
  const [postData, setpostData] = useState({
    id,
    token,
    address_id,
    page: 1
  });
  const initData = {
    id,
    token,
    address_id,
    page: 1,
    search: "",
    reviewed: 'O',
  };

  useEffect(() => {
    getUser(initData);
    console.log(tableData);

    return () => {};
  }, []);
  async function reset() {
    setpostData(initData);
    getUser(initData);
  }
  async function onPageChange(e) {
    setpostData({ ...postData, page: e });
    getUser({ ...postData, page: e });
  }
  async function getUser(Data = { ...postData }) {
    setLoading(true);
    console.log("Data", Data);
    try {
      const response = await Api.post(
        "/admin/Driver/driver_list",

        qs.stringify(Data)
      );
      console.log(response);
      response.data.error_code === 0 && settableData(response.data);
      // if (response.data.error_code == -101) {
			// 	window.location.href = '../login/login.html';
			// }
      setLoading(false);
    } catch (error) {
      console.error(error);
    }
  }
  async function handleChange(d_id, operation, address_id) {
    setLoading(true);

    let data = { d_id, operation, address_id };
    try {
      const response = await Api.post(
        "/admin/Driver/up_operation",

        qs.stringify(data)
      );
      console.log(response);
      getUser(initData);

      message.success("操作成功");
    } catch (error) {
      message.success("操作失败");
    }
  }
  function searchChange(d_id) {
    console.log("搜索按钮");
  }
  async function Banned(day,d_id) {
    let data={
      id,token,day,d_id,address_id
    }
    try {
      const response = await Api.post(
        "/admin/Driver/banned_driver",
        qs.stringify(data)
      );
      console.log(response);
      getUser()
      setvisible(false)
      message.success("操作成功");
    } catch (error) {
      message.success("操作失败");
    }
  }
  async function unblock(record) {
    console.log(record);
    let data = {id,token,d_id:record.d_id,address_id};

    if (record.available_pd == "Y") {
      message.info("状态正常");
      return false;
    } else {
      confirm({
        title: "确认解封?",
        content: "",
        async onOk() {
          try {
            const response = await Api.post(
              "/admin/Driver/unbanned_driver",
      
              qs.stringify(data)
            );
            console.log('解封',response);
            getUser();
      
            message.success("操作成功");
          } catch (error) {
            message.success("操作失败");
          }

        },
        onCancel() {}
      });
    }

  
  }
  async function detail(d_id) {
    setLoading(true);

    // try {
    //   const response = await Api.post(
    //     "/admin/Driver/up_operation",

    //     qs.stringify(data)
    //   );
    //   console.log(response);
    //   getUser(initData);

    //   message.success("操作成功");
    // } catch (error) {
    //   message.success("操作失败");
    // }
  }

  const columns = [
    {
      fixed: "left",
      title: "司机姓名",
      width: 100,
      dataIndex: "name",
      key: "name"
    },
    {
      fixed: "left",
      title: "司机电话",
      width: 100,
      dataIndex: "phone",
      key: "phone"
    },
    {
      title: "司机工号",
      dataIndex: "card",
      key: "card"
    },
    {
      title: "车牌号",
      dataIndex: "carnum",
      key: "carnum"
    },
    {
      title: "车辆类型",
      dataIndex: "car_type",
      key: "car_type"
    },
    {
      title: "车辆颜色",
      dataIndex: "carcolor",
      key: "carcolor"
    },
    {
      title: "注册时间",
      dataIndex: "sdate",
      key: "sdate"
    },
    {
      title: "到期时间",
      key: "maturity_date",
      dataIndex: "maturity_date"
    },
    {
      title: "审核状态",
      dataIndex: "reviewed_cn",
      key: "reviewed_cn"
    },
    {
      title: "司机行车状态",
      dataIndex: "state_cn",
      key: "state_cn"
    },
  
    {
      title: "操作",
      key: "action",
      render: (text, record) => (
        <div>
          <Button size='small' onClick={() => {
              setD_id(record.d_id);
              setInfoVisible(true);
            }}>查看</Button>
        </div>
      )
    }
  ];

  return (
    <div>
      <Card>
        <Row>
          <Col span={6}>
            <Input
              value={postData.search}
              placeholder="请输入司机工号，司机姓名或司机手机号"
              onChange={e =>
                setpostData({ ...postData, search: e.target.value })
              }
            />
          </Col>
          <Col span={1} offset={1}>
            <Button onClick={() => getUser()}>搜索</Button>
          </Col>
          <Col span={1} offset={1}>
            <Button onClick={reset}>重置</Button>
          </Col>
       
        </Row>
      </Card>
      <Card>
        <Table
          columns={columns}
          loading={loading}
          dataSource={tableData.list}
          pagination={{
            current: postData.page,
            total: Number(tableData.count),
            onChange: e => onPageChange(e),
            disabled: loading,
            showQuickJumper: true,
            showTotal: total => `共${total}条`
          }}
        />
        ;
      </Card>
      <Modal
      destroyOnClose={true}
      width='80%'
        title="司机信息"
        visible={infoVisible}
        
        onOk={() => Banned(day, d_id)}
        onCancel={() => setInfoVisible(false)}
      >
       <DriverInfo d_id={d_id}></DriverInfo>
      </Modal>
      <Modal
        title="确认封禁?"
        visible={visible}
        onOk={() => Banned(day, d_id)}
        onCancel={() => setvisible(false)}
      >
        <Select style={{ width: "100%" }} onChange={e => setDay(e)}>
          <Option value="1">1天</Option>
          <Option value="2">2天</Option>
          <Option value="3">3天</Option>
          <Option value="7">7天</Option>
          <Option value="15">15天</Option>
          <Option value="forever">永久</Option>
        </Select>
      </Modal>
    </div>
  );
}

export default DriverList;
