import React, { useState, useEffect } from "react";
import {
  Table,
  Button,
  Select,
  Card,
  Input,
  message,
  Row,
  Col,
  Modal
} from "antd";
import { Api, baseURL } from "../api";
import qs from "qs";
import { Link } from "react-router-dom";
import DriverInfo from './DriverInfo'
const { confirm } = Modal;
const { Option } = Select;
const id = localStorage.getItem("userid");
const token = localStorage.getItem("token");
const address_id = localStorage.getItem("address_id");
function DriverList() {
  const [day, setDay] = useState(1);
  const [d_id, setD_id] = useState();
  const [infoVisible, setInfoVisible] = useState(false);

  const [visible, setvisible] = useState(false);
  const [loading, setLoading] = useState(true);

  const [tableData, settableData] = useState({});
  const [postData, setpostData] = useState({
    id,
    token,
    address_id,
    page: 1
  });
  const initData = {
    id,
    token,
    address_id,
    page: 1,
    search: "",
    reviewed: "",
    state: "",
  };

  useEffect(() => {
    setpostData(initData);
    getUser(initData);
    console.log(tableData);

    return () => {};
  }, []);
  async function reset() {
    setpostData(initData);
    getUser(initData);
  }
  async function onPageChange(e) {
    setpostData({ ...postData, page: e });
    getUser({ ...postData, page: e });
  }
  async function exportExcel() {
    let {state,reviewed}=postData
    if (state == ''&& reviewed!='') {
			window.location.href = baseURL +'/admin/Exportdata/driverlistexporde/reviewed/' + reviewed+'/address_id/' + address_id;
		}
		if(reviewed==''&& state!=''){
			window.location.href = baseURL +'/admin/Exportdata/driverlistexporde/state/' + state+'/address_id/' + address_id;
		}if(state!='' && reviewed!=''){
			window.location.href = baseURL +'/admin/Exportdata/driverlistexporde/state/' + state+'/reviewed/' + reviewed+'/address_id/' + address_id;
		}if(state=='' && reviewed==''){
			window.location.href = baseURL +'/admin/Exportdata/driverlistexporde/address_id/' + address_id;
		}

  }
  async function getUser(Data = { ...postData }) {
    setLoading(true);
    console.log("Data", Data);
    try {
      const response = await Api.post(
        "/admin/Driver/driver_list",

        qs.stringify(Data)
      );
      console.log(response);
      response.data.error_code === 0 && settableData(response.data);
      // if (response.data.error_code == -101) {
			// 	window.location.href = '../login/login.html';
			// }
      setLoading(false);
    } catch (error) {
      console.error(error);
    }
  }
  async function handleChange(d_id, operation, address_id) {
    setLoading(true);

    let data = { d_id, operation, address_id };
    try {
      const response = await Api.post(
        "/admin/Driver/up_operation",

        qs.stringify(data)
      );
      console.log(response);
      getUser(initData);

      response.data.error_code === 0
      ? message.success("操作成功")
      : message.warning(response.data.message);
    } catch (error) {
      message.success("操作失败");
    }
  }
  function searchChange() {
    getUser({...postData,page:1})
   }
  async function Banned(day,d_id) {
    let data={
      id,token,day,d_id,address_id
    }
    try {
      const response = await Api.post(
        "/admin/Driver/banned_driver",
        qs.stringify(data)
      );
      console.log(response);
      getUser()
      setvisible(false)
      response.data.error_code === 0
      ? message.success("操作成功")
      : message.warning(response.data.message);
    } catch (error) {
      message.success("操作失败");
    }
  }
  async function unblock(record) {
    console.log(record);
    let data = {id,token,d_id:record.d_id,address_id};

    if (record.available_pd == "Y") {
      message.info("状态正常");
      return false;
    } else {
      confirm({
        title: "确认解封?",
        content: "",
        async onOk() {
          try {
            const response = await Api.post(
              "/admin/Driver/unbanned_driver",
      
              qs.stringify(data)
            );
            console.log('解封',response);
            getUser();
      
            response.data.error_code === 0
            ? message.success("操作成功")
            : message.warning(response.data.message);
          } catch (error) {
            message.success("操作失败");
          }

        },
        onCancel() {}
      });
    }

  
  }
  async function detail(d_id) {
    setLoading(true);

    // try {
    //   const response = await Api.post(
    //     "/admin/Driver/up_operation",

    //     qs.stringify(data)
    //   );
    //   console.log(response);
    //   getUser(initData);

    //   message.success("操作成功");
    // } catch (error) {
    //   message.success("操作失败");
    // }
  }

  const columns = [
    {
      fixed: "left",
      title: "司机姓名",
      width: 100,
      dataIndex: "name",
      key: "name"
    },
    {
      fixed: "left",
      title: "司机电话",
      width: 100,
      dataIndex: "phone",
      key: "phone"
    },
    {
      title: "司机工号",
      dataIndex: "card",
      key: "card"
    },
    {
      title: "车牌号",
      dataIndex: "carnum",
      key: "carnum"
    },
    {
      title: "车辆类型",
      dataIndex: "car_type",
      key: "car_type"
    },
    {
      title: "车辆颜色",
      dataIndex: "carcolor",
      key: "carcolor"
    },
    {
      title: "邀请注册数量",
      key: "invitCount",
      dataIndex: "invitCount",
      render: (text, record) => (
        <div>
          <Link to={"/invitcount/" + record.d_id}>{record.invitCount}</Link>
        </div>
      )
    },
    {
      title: "注册时间",
      dataIndex: "sdate",
      key: "sdate"
    },
    {
      title: "审核状态",
      dataIndex: "reviewed_cn",
      key: "reviewed_cn"
    },
    {
      title: "司机接单状态",
      dataIndex: "state_cn",
      key: "state_cn"
    },
    {
      title: "禁封状态",
      dataIndex: "available",
      key: "available"
    },
    {
      title: "到期时间",
      key: "maturity_date",
      dataIndex: "maturity_date"
    },
    {
      title: "运营方式",
      key: "operation",
      dataIndex: "operation",
      render: (text, record) =>
        address_id != 0 ? (
          <div>您不是超级管理员 {address_id}</div>
        ) : (
          <div>
            <Select
              value={record.operation}
              onChange={e => handleChange(record.d_id, e, record.address_id)}
            >
              <Option value="taxi">出租车</Option>
              <Option value="car">非出租车</Option>
            </Select>
          </div>
        )
    },
    {
      title: "地区",
      key: "address_id",
      dataIndex: "address_id",
      render: (text, record) =>
        address_id != 0 ? (
          <div>您不是超级管理员 {address_id}</div>
        ) : (
          <div>
            <Select
              value={record.address_id}
              onChange={e => handleChange(record.d_id, record.operation, e)}
            >
              <Option value="1">宝砥</Option>
              <Option value="2">盐山</Option>
              <Option value="3">大城</Option>
            </Select>
          </div>
        )
    },
    {
      fixed: "right",
      title: "操作",
      key: "action",
      render: (text, record) => (
        <div>
          <Button size='small' onClick={() => {
              setD_id(record.d_id);
              setInfoVisible(true);
            }}>查看</Button>
          <Button
            ghost
            size='small'
            onClick={() => {
              setvisible(true);
              setD_id(record.d_id);
            }}
            type="danger"
          >
            封禁
          </Button>
          <Button size='small' onClick={() => unblock(record)} ghost type="primary">
            解封
          </Button>
        </div>
      )
    }
  ];

  return (
    <div>
      <Card>
        <Row>
          <Col span={6}>
            <Input
              value={postData.search}
              placeholder="请输入司机工号，司机姓名或司机手机号"
              onChange={e =>
                setpostData({ ...postData, search: e.target.value })
              }
            />
          </Col>

          <Col span={3} offset={1}>
            <Select
              style={{ width: "100%" }}
              value={postData.reviewed}
              defaultValue="default"
              placeholder="司机审核状态"
              onChange={e => {
                console.log("e", e);
                setpostData({ ...postData, reviewed: e });
              }}
            >
              <Option value="">全部</Option>
              <Option value="Y">审核通过</Option>
              <Option value="N">审核未通过</Option>
              <Option value="O">审核中</Option>
            </Select>
          </Col>
          <Col span={3} offset={1}>
            <Select
              style={{ width: "100%" }}
              defaultValue="default"
              value={postData.state}
              placeholder="司机接单状态"
              onChange={e => {
                console.log("e", e);
                setpostData({ ...postData, state: e });
              }}
            >
              <Option value="">全部</Option>
              <Option value="off">关闭</Option>
              <Option value="stand">待接单</Option>
              <Option value="running">行程中</Option>
            </Select>
          </Col>

          <Col span={1} offset={1}>
            <Button onClick={() => searchChange()}>搜索</Button>
          </Col>
          <Col span={1} offset={1}>
            <Button onClick={reset}>重置</Button>
          </Col>
          <Col span={1} offset={1}>
            <Button onClick={exportExcel}>导出</Button>
          </Col>
        </Row>
      </Card>
      <Card>
        <Table
          scroll={{ x: "130%" }}
          columns={columns}
          loading={loading}
          dataSource={tableData.list}
          pagination={{
            current: postData.page,
            total: Number(tableData.count),
            onChange: e => onPageChange(e),
            disabled: loading,
            showQuickJumper: true,
            showTotal: total => `共${total}条`
          }}
        />
      </Card>
      <Modal
      destroyOnClose={true}
      width='80%'
        title="司机信息"
        visible={infoVisible}
        onCancel={() => setInfoVisible(false)}
        footer={[
          <Button key="back" onClick={() => setInfoVisible(false)}>
            关闭
          </Button>,
          // <Button key="submit" type="primary" loading={loading} onClick={this.handleOk}>
          //   Submit
          // </Button>,
        ]}
      >
       <DriverInfo d_id={d_id}></DriverInfo>
      </Modal>
      <Modal
        title="确认封禁?"
        visible={visible}
        onOk={() => Banned(day, d_id)}
        onCancel={() => setvisible(false)}
      >
        <Select style={{ width: "100%" }} onChange={e => setDay(e)}>
          <Option value="1">1天</Option>
          <Option value="2">2天</Option>
          <Option value="3">3天</Option>
          <Option value="7">7天</Option>
          <Option value="15">15天</Option>
          <Option value="forever">永久</Option>
        </Select>
      </Modal>
    </div>
  );
}

export default DriverList;
