import React, { useState, useEffect } from "react";
import { Table, Button, Card, message, Row, Col, DatePicker } from "antd";

import { Api } from "../api";
import qs from "qs";
import { useParams } from "react-router-dom";
import moment from "moment";
import "moment/locale/zh-cn";
moment.locale("zh-cn");
const { RangePicker } = DatePicker;

const token = localStorage.getItem("token");
const address_id = localStorage.getItem("address_id");
function DriverList() {
  let { driverId } = useParams();
  console.log(driverId);
  const [tableData, settableData] = useState({});
  const initData = {
    driverId,
    token,
    page: 1,
    startTime: "",
    endTime: ""
  };
  const [postData, setpostData] = useState(initData);

  const [loading, setLoading] = useState(true);

  useEffect(() => {
    getUser(initData);
    console.log(tableData);

    return () => {};
  }, []);
  async function reset() {
    setpostData(initData);
    getUser(initData);
  }
  async function onPageChange(e) {
    setpostData({ ...postData, page: e });
    getUser({ ...postData, page: e });
  }
  async function getUser(Data = { ...postData }) {
    setLoading(true);
    console.log("Data", Data);
    try {
      const response = await Api.post(
        "/admin/Driver/get_invitUser_list",

        qs.stringify(Data)
      );
      console.log(response);
      response.data.error_code === 0 && settableData(response.data);

      setLoading(false);
    } catch (error) {
      console.error(error);
    }
  }
  async function handleChange(d_id, operation, address_id) {
    setLoading(true);

    let data = { d_id, operation, address_id };
    try {
      const response = await Api.post(
        "/admin/Driver/up_operation",

        qs.stringify(data)
      );
      console.log(response);
      getUser(initData);

      message.success("操作成功");
    } catch (error) {
      message.success("操作失败");
    }
  }
  function searchChange() {
    console.log("搜索按钮");
  }

  const columns = [
    {
      title: "头像",
      key: "headimgurl",

      render: (text, record) => (
        <img
          style={{ width: "100px", height: "100px" }}
          src={record.headimgurl}
        ></img>
      )
    },

    {
      title: "昵称",
      dataIndex: "nickname",
      key: "name"
    },
    {
      title: "电话",
      dataIndex: "phone",
      key: "phone"
    },
    {
      title: "注册时间",
      dataIndex: "sdate",
      key: "sdate"
    }

    // {
    //   title: "操作",
    //   key: "action",
    //   render: (text, record) => (
    //     <div>
    //       <Button>查看</Button>
    //       <Button ghost type="danger">
    //         封禁
    //       </Button>
    //       <Button ghost type="primary">
    //         解封
    //       </Button>
    //     </div>
    //   )
    // }
  ];
  return (
    <div>
      <Card>
        <Row>
          <Col span={4}>
            <RangePicker
              onChange={(value, dateString) => {
                setpostData({
                  ...postData,
                  startTime: dateString[0],
                  endTime: dateString[1]
                });
              }}
            />
          </Col>

          <Col span={1} offset={1}>
            <Button onClick={() => getUser()}>搜索</Button>
          </Col>
          <Col span={1} offset={1}>
            <Button onClick={reset}>重置</Button>
          </Col>
        </Row>
      </Card>
      <Card>
        <Table
          columns={columns}
          loading={loading}
          dataSource={tableData.list}
          pagination={{
            current: postData.page,
            total: tableData.count,
            onChange: e => onPageChange(e),
            disabled: loading,
            showQuickJumper: true,
            showTotal: total => `共${total}条`,

          }}
        />
        ;
      </Card>
      ,
    </div>
  );
}

export default DriverList;
