import React,{useState,useEffect} from "react";
import {
    Button,
    Card,
    Input,
    Row,
    Col,
    Table
} from "antd";
import {Api,baseURL} from "../api";
import qs from "qs";
const id = localStorage.getItem("userid");
const token = localStorage.getItem("token");
const address_id = localStorage.getItem("address_id");
function DriverOnDuty(){
    const [tableData,setTableData] = useState({});
    const [postData,setPostData] = useState({
        id,
        token,
        address_id,
        page:1
    });
    const initData = {
        id,
        token,
        address_id,
        search:"",
        page:1
    };
    useEffect(()=>{
        getDriverOnDuty(initData);
        console.log(tableData);
        return ()=>{};
    },[]);
    async function getDriverOnDuty(Data = {...postData}){
        try{
            const response = await Api.post("/admin/Analysis/driver_onlinetime",qs.stringify(Data));
            console.log(response);
            response.data.error_code === 0 && setTableData(response.data);
        }catch(error){
            console.error(error)
        }
    }

    const columns = [
        {
          title:"司机工号",
            dataIndex:'card'
        },
        {
            title:"司机姓名",
            dataIndex:"name"
        },
        {
            title:"司机电话",
            dataIndex:"phone"
        },
        {
            title:"在线时长",
            dataIndex:"dutyTime"
        },
        {
            title:"操作",
            dataIndex:"action"
        }
    ];
    return (
        <div>
            <Card>
                <Row gutter={1} row-flex={'center'}>
                    <Col span={6}>
                        <Input
                            placeholder={'请输入司机工号，司机姓名或司机手机号'}
                        />
                    </Col>
                    <Col span={2} offset={1}>
                       开始时间
                    </Col>
                    <Col span={3}>
                        <Input
                            placeholder={'请选择开始时间'}
                        />
                    </Col>
                    <Col span={2} offset={1}>
                        结束时间
                    </Col>
                    <Col span={3}>
                        <Input
                            placeholder={'请选择结束时间'}
                        />
                    </Col>
                    <Col span={1}>
                        <Button onClick={()=>{
                            getDriverOnDuty()
                        }}>搜索</Button>
                    </Col>
                    <Col span={1} offset={1}>
                        <Button>重置</Button>
                    </Col>
                    <Col span={1} offset={1}>
                        <Button>导出</Button>
                    </Col>
                </Row>
            </Card>
            <Card>
                <Table
                    columns={columns}
                    dataSource={tableData.list}
                />
            </Card>
        </div>
    )
}
export default DriverOnDuty;